import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ValidationModel } from './../../../models/validation.class';
import { LoginService } from './../../../services/LoginService';
import * as $ from 'jquery';

@Component({
  selector: 'app-basic-login',
  templateUrl: './basic-login.component.html',
  styleUrls: ['./basic-login.component.scss'],
  providers: [LoginService]
})
export class BasicLoginComponent implements OnInit {
  public remember_Me_Value: Boolean = false;

  public Username: string = "";
  public Password: string = "";

  constructor(public _loginService: LoginService, public router: Router) { }

  ngOnInit() {
  }

  rememberMe() {
    this.remember_Me_Value = true;
  }// End of Remember Me Function

  public runner: boolean = false;

  login() {
    if (this.runner == false) {
      $('.error').hide();
      $('#loader').show();
      this.runner = true;
      console.log(ValidationModel.validateString(this.Username));
      if (ValidationModel.validateString(this.Username)) {
        this.runner = false;
        $('#username').show();
        return;
      }
      if (ValidationModel.validateString(this.Password)) {
        this.runner = false;
        $('#password').show();
        return;
      }
      this._loginService.login(this.Username, this.Password).subscribe(res => {
        $('#loader').hide();
        if (res.code == 200) {
          $('.error').hide();
          this.runner = false;
          console.log(res);
          localStorage.setItem('session_obj', JSON.stringify(res.data));
          this.router.navigateByUrl('/admin/dashboard/ecommerce');
        }
        else {
          $('.error').hide();
          this.runner = false;
          $("#serviceErrors").text(res.message);
          $("#serviceErrors").show();
        }
      });
    }
  }
}



